import React from 'react';
import ItemList from './itemList';
import './Itemmsg.css'



class Itemmsg extends React.Component {
    
    render(props) {
        // console.log(this.props);
        
        let id = this.props.match.params.id;
        // console.log(Number(id.slice(1, id.length)));
        let item = ItemList[Number(id.slice(1, id.length)) - 1];

        return <div className='body'>
            <i className="iconfont icon-fanhui"  onClick={()=>this.props.history.goBack()}></i>
            <img src={item.itemSrc} className='img'></img>
            <p>{item.itemPrise}</p>
            <p>{item.itemName}</p>
            
            <div className='bott'>
                <div className='but'>
                    <i className="iconfont icon-dianpu but_1"></i>
                    <p>店铺</p>
                </div>
                <div className='but'>
                    <i className="iconfont icon-kefu but_2"></i>
                    <p>客服</p>
                </div>
                <div className='but'>
                    <i className="iconfont icon-shoucang but_3"></i>
                    <p>收藏</p>
                </div>

                <div className='tbut'>
                    <button className='but_4'>加入购物车</button>
                    <button className='but_5'>立即购买</button>
                </div>
            </div>
            
        </div>
    }
}




export default Itemmsg;