//Redux-提供可预测的状态管理的容器
//解决跨层级传递属性的问题
//action-是更新状态的唯一途径，就是一个普通对象
// 一定包含一个type属性
// reducer-是一个纯函数
// 没有副作用，有相同的输入，一定会有相同的输出
// store链接action和Reducer

let obj = {username:'123'}
let obj1 = Object.assign({}, obj, {username:'456'}, {a:100})
console.log(obj === obj1);
console.log(obj, obj1);

const App = ()=>{
	return <Provider store={store}>
		<Router>
			<Home />
			<Input />
			<Todos />
		</Router>
	</Provider>
}
export default App;

