//路由基本配置
function PrivateRoute({ component:Com, ...rest }) {
		return (
			<Route
				{...rest}
				render={({ location }) =>
				localStorage.getItem('userinfo')
				?<Com {...rest}/> 
				: (<Redirect to={{pathname: "/login", state: location }}/>)
				}
			/>
		);	
  }
  function RouteWithSubRoutes(route) {
	//   console.log(route);
	//   this.props.route = route;
	return (route.pri? <PrivateRoute {...route}/>:<Route
		path={route.path}
		render={props => (
		  // pass the sub-routes down to keep nesting
		  <route.component {...props} routes={route.routes} />
		)}
	  />
	);
  }

