import React from 'react'
import {connect} from 'react-redux'
const Todos = (props) => {
    console.log(props);
    return (
        <div>
            {
                props.mydata.map((item, i)=><li key={i}>{item}<button onClick={()=>props.dispatch({
                    type:"DEL_TODO",
                    index:i
                })}>del</button></li>)
            }
            <button 
                onClick={()=>props.dispatch({
                    type:"ADD_TODO",
                    value:'100000'
                })}
            >添加</button>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        mydata:state.todo
    }
}

export default connect(mapStateToProps)(Todos)