import React from 'react';
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom'
import Tbhome from './Tbhome';
import Mytab from './Mytab';
import List from './List';
import Mytaobao from './Mytaobao';
import Shopcart from './Shopcart';
import Search from './Search';
import Itemmsg from './Itemmsg';


const Tbapp = () => {
	return <Router>
		<Switch>
			<Route exact path="/" component={Tbhome}/>
			<Route path='/shopcart' component={Shopcart}/>
			<Route path='/list' component={List}/>
			<Route path='/mytaobao' component={Mytaobao}/>
			<Route path='/search' component={Search}/>
			<Route path='/itemmsg/:id' component={Itemmsg}/>
			{/* <Route component={Tbhome}/> */}
			<Route render={()=><Redirect to='/'/>}/>

		</Switch>
        <Mytab />
	</Router>
}

export default Tbapp;
